import axiosInstance from './index'

const axios = axiosInstance

// 
export const postUser = (username, password) => {
  return axios.post('http://47.104.15.57/login/', {
    'username': username, 'password': password
  })
}

// 
export const postNewUser = (username, password, pwd2, tel, email) => {
  return axios.post('http://47.104.15.57/users/', {
    'username': username, 'password': password, 'pwd2': pwd2, 'tel': tel, 'email': email
  })
}

// 
export const changeUserInfo = (id, username, password, email, intro) => { // 'head': url
  return axios.put('http://47.104.15.57/users/' + id + '/', {
    'username': username, 'password': password, 'email': email, 'introduction': intro,
  })
}

// 
export const getUserInfo2 = (id) => {
  return axios.get('http://47.104.15.57/users/', {
    params: {id: id}})
}

// 
export const getUserInfo = () => {
  return axios.get('http://47.104.15.57/users/1')
}

// 
export const getCategory = () => {
  return axios.get('http://47.104.15.57/category/')
}

// 
export const submitBlog = (title, body, category) => { //  'image': image, 'file': file
  return axios.post(`http://47.104.15.57/edit/`, {
    'title': title, 'body': JSON.stringify(body), 'category': category
  })
}

// 
export const submitComment = (BlogID, body) => {
  return axios.post(`http://47.104.15.57/comment/`, {
    'post': BlogID, 'body': JSON.stringify(body)
  })
}

// 
export const submitComment2 = (BlogID, body, commentID) => {
  return axios.post(`http://47.104.15.57/comment/`, {
    'post': BlogID, 'reply_comment': commentID, 'body': JSON.stringify(body)
  })
}

// 
export const toFollow = (authorID) => {
  return axios.post('http://47.104.15.57/userfavs/', {
    'goods': authorID
  })
}

// 
export const getFollowInfo = () => {
  return axios.get('http://47.104.15.57/userfavs/')
}

// 
export const Like = (BlogID) => {
  return axios.post('http://47.104.15.57/like/', {
    'post': BlogID
  })
}

// 
export const getLikeInfo = () => {
  return axios.get('http://47.104.15.57/like/')
}



