<template>
  <div v-title data-title="">
    <UserNav></UserNav>
    <el-form rules="rules" ref="infoForm" :model="userd" label-width="120px" v-if="rightUser">
      <el-form-item prop="passwordd">
        
        <input type="password" v-model="userd.passwordd"></input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="deleteUser"></el-button>
        <el-button type="danger" ></el-button>
      </el-form-item>
    </el-form>
    <div v-else>
      
    </div>
  </div>
</template>

<script>
import UserNav from './UserCenter'
export default {
  name: 'DeleteUser',
  data () {
    return {
      rightUser: true,
      user: {
        UserName: 'User',
        password: '12345'
      },
      userd: {
        passwordd: ''
      },
      rules: {
        passwordd: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  components: {
    UserNav
  },
  mounted () {
    this.user.UserName = this.$route.params.UserId
    if (this.GLOBAL.username === this.user.UserName) {
      this.rightUser = true
    } else this.rightUser = false
  },
  methods: {
    deleteUser () {
      if (this.userd.passwordd === this.user.password) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      } else {
        this.$message({
          type: 'warning',
          message: ''
        })
      }
    }
  }
}
</script>

<style scoped>
  .el-message-box {
    z-index: 2000;
  }
</style>



