<template>
  <div v-title data-title="">
    <br><br>
    <el-tabs v-model="activeName" type="card">
      <el-tab-pane label="" name="first">
        <el-container style="margin-right:250px;margin-left:250px; margin-top:30px;">
          <el-container style="" direction="vertical">
            <el-form ref="infoForm" :model="Blog" :rules="rules" label-width="120px" enctype="multipart/form-data">
              <el-form-item label="" prop="title">
                <el-input
                    type="textarea"
                    placeholder=""
                    v-model="Blog.title"
                    maxlength="30"
                    show-word-limit
                    style="width: 70%; float: left"
                  >
                </el-input>
              </el-form-item>

              <el-form-item label="" prop="simple">
                <el-input
                    type="textarea"
                    placeholder="30"
                    v-model="Blog.simple"
                    maxlength="30"
                    show-word-limit
                    style="width: 70%; float: left"
                  >
                </el-input>
              </el-form-item>

              <el-form-item label="" prop="tags">
                <div class="grid-content" style="text-align:left; overflow:visible;">
                  <br>
                  <!---->
                  <el-select
                    v-model="Blog.tags"
                    filterable
                    default-first-option
                    size="medium"
                    placeholder="">
                    <el-option
                      v-for="item in Blog.options"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </div>
              </el-form-item>

              <el-form-item label="" prop="content">
                <myEditor v-model="Blog.content" :isClear="isClear"></myEditor>

              </el-form-item>

              <el-form-item>
                <el-upload
                  class="upload-demo"
                  drag
                  ref="upload"
                  action="url"
                  :auto-upload="false"
                  :http-request="uploadFile"
                  :file-list="fileList"
                  :on-change="handleChange"
                  :limit=1>
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text"><em></em></div>
                </el-upload>
              </el-form-item>

              <el-form-item>
                <el-button type="primary" @click="onSubmit"></el-button>
                <el-button type="danger" ></el-button>
              </el-form-item>
            </el-form>
          </el-container>
        </el-container>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <el-upload
          class="upload-demo"
          drag
          ref="upload2"
          action="url"
          :auto-upload="false"
          :http-request="uploadFile"
          :file-list="fileList"
          :on-change="handleChange"
          :limit=1>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
        </el-upload>
        <el-button @click="Submit2"></el-button>
      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
// import axiosInstance from '../../api/index'
import myEditor from './wangEditor'
import {getCategory, submitBlog} from '../../api/api'
import axiosInstance from '../../api/index'
export default {
  name: 'test',
  data () {
    return {
      url: 'http://47.104.15.57/files/',
      activeName: 'first',
      UserId: 'User',
      urls: {
        Home: '/',
        UpdateBlog: '/UpdateBlog'
      },
      Blog: {
        title: '',
        simple: '',
        content: '',
        options: [{
          name: 'HTML',
          id: 'HTML'
        }, {
          name: 'java',
          id: 'java'
        }],
        tags: '' // value
      },
      header: {token: sessionStorage.token},
      // 
      rules: {
        title: [
          {required: true, message: '', trigger: 'blur'}
        ],
        tags: [
          {required: true, message: '', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '', trigger: 'blur'}
        ]
      },
      newBlogId: '',
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      fileList: [],
      isClear: false
      // param:''
    }
  },
  methods: {
    log (value) {
      this.tags = value
      console.log(this.tags, value)
    },
    Submit2(){
      this.$refs.upload2.submit()
      console.log('upload file only done')
      this.$router.push('/MainPage')
    },
    handleChange (file, fileList) {
      this.fileList = fileList
      console.log(this.fileList)
    },
    uploadFile () {
      console.log('uploading files')
      let axios = axiosInstance
      let formData = new FormData()
      formData.append('file', this.fileList[0].raw)
      formData.append('post', this.newBlogId)
      formData.append('name', this.fileList[0].name)
      axios.post(this.url, formData, {headers: {'Content-Type': 'multipart/form-data' }}).then(response => {
        if (response.status === 201) {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
        }
      })
    },
    onSubmit () {
      this.$refs.infoForm.validate((valid) => {
        if (valid) { // 
          submitBlog(this.Blog.title, this.Blog.content, this.Blog.tags).then(response => {
            if (response.status === 201) {
              console.log(response)
              this.newBlogId = response.data.id
              this.$refs.upload.submit()
              this.$router.push('/MainPage')// 
            } else {
              console.log(response)
              this.$message({
                message: '',
                type: 'error'
              })
            }
          })
        }
      })
    },
    getCategory () {
      getCategory().then(response => {
        if (response.status === 200) {
          // let i = 0
          // let sum = response.data.length
          // for (i = 0; i < sum; i++) {
          //   this.Blog.options[i].label = response.data[i].name
          //   this.Blog.options[i].value = response.data[i].id
          // }
          this.Blog.options = response.data
        }
      })
    }
  },
  components: {
    myEditor
  },
  mounted () {
    this.getCategory()
  }
}

</script>

<style scoped>
  .el-message-box {
    z-index: 2000;
  }
</style>



