<template>
  <div>
    <div class="editor">
      <div ref="editor"style="min-height: 300px;text-align: left"></div>
    </div>
  </div>
</template>

<script>
  import E from 'wangeditor'
  export default {
    name: "wangEditor",
    data(){
      return{
        editor:null,
        info:'...'
      }
    },
    model:{
      prop: 'value',
      event: 'change'
    },
    props:{
      value:String,
      isClear:Boolean
    },
    watch:{
      isClear(val){
        if(val){
          this.editor.txt.clear()
          this.info = null
        }
      },
      value(val){
        this.editor.txt.html(val)
      }
    },
    mounted() {
      this.setEditor()
    },
    methods:{
      setEditor(){
        this.editor = new E(this.$refs.editor)//refs.ref=
          this.editor.customConfig.onchange = (html) => {
            this.info = html
            this.$emit('change', this.info)
          }
        this.editor.customConfig.uploadImgShowBase64 = true  // 
        this.editor.customConfig.pasteFilterStyle = false// 
        //  tab  tab 
        this.editor.customConfig.emotions = [
            {
                // tab 
                title: '',
                // type -> 'emoji' / 'image'
                type: 'image',
                // content -> 
                content: [
                    {
                        alt: '[]',
                        src: 'http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/50/pcmoren_huaixiao_org.png'
                    },
                    {
                        alt: '[]',
                        src: 'http://img.t.sinajs.cn/t4/appstyle/expression/ext/normal/40/pcmoren_tian_org.png'
                    }
                ]
            },
            {
                // tab 
                title: 'emoji',
                // type -> 'emoji' / 'image'
                type: 'emoji',
                // content -> 
                content: ['', '', '', '', '']
            }
        ]
        this.editor.customConfig.menus=[
           'head',  // 
          'bold',  // 
          'fontSize',  // 
          'fontName',  // 
          'italic',  // 
          'underline',  // 
          'strikeThrough',  // 
          'foreColor',  // 
          'backColor',  // 
          'link',  // 
          'list',  // 
          'justify',  // 
          'quote',  // 
          'emoticon',  // 
          'image',  // 
          'table',  // 
          //'video',  // 
          'code',  // 
          'undo',  // 
          'redo'  // 
        ]
        this.editor.customConfig.zIndex = 100
        this.editor.create()
        //editor.$textElem.attr('contenteditable', false)//
      }
    }
  }
</script>

<style scoped>
  .editor{
    width: 100%;
    text-align: left;
  }
  .toolbar{
    border: 1px solid #ccc;
  }
  .text{
    border: 1px solid #ccc;
    height: 500px;
  }
  .w-e-toolbar{
    flex-wrap:wrap;
  }
  .w-e-text-container{
    height: 700px !important;
  }
</style>



